export class CategoryModel {
    id: number;
    name: string;
    image: string;
    status: boolean;
    created_at: Date;
    updated_at: Date;

    constructor() {
        this.name = '';
        this.image = '';
    }

    loadModel(response: any) {
        this.id = response.id;
        this.name = response.name;
        this.image = response.image;
        this.status = response.status;
        this.created_at = response.created_at;
        this.updated_at = response.updated_at;
    }
}
