import { CategoryModel } from './category.model';
import { UserModel } from './user.model';
import { AddressModel } from './address.model';
import { IImage } from 'ng-simple-slideshow';

export class SpaceModel {
    id: number;
    name: string;
    category_id: CategoryModel;
    description: string;
    // user_id: UserModel;
    user: UserModel;
    price: number;
    address: AddressModel;
    capacity: number;
    rules: string;
    space_tags: any;
    space_images: any;
    minimum_hours: number;
    space_favorites: any;
    images: IImage[];
    liked: boolean;
    constructor() {
        this.name = '';
        this.category_id = new CategoryModel();
        // this.user_id = new UserModel();
        this.user = new UserModel();
        this.description = '';
        this.rules = '';
        this.address = new AddressModel();
        this.liked = false;
        this.images = [];
    }

    loadModel(response: any) {
        this.id = response.id;
        this.name = response.name;
        this.category_id = response.category_id;
        this.description = response.description;
        // this.user_id = response.user_id;
        this.user = response.user;
        this.price = response.price;
        this.address = response.address_id;
        this.capacity = response.capacity;
        this.rules = response.rules;
        this.liked = response.liked;
    }
}
